.round-button {
  margin: 10px;
  box-sizing: border-box;
  display: block;
  width: 200px;
  height: 200px;
  padding-top: 14px;
  padding-left: 8px;
  line-height: 20px;
  border: 6px solid #fff;
  border-radius: 50%;
  color: #f5f5f5;
  text-align: center;
  text-decoration: none;
  background-color: rgba(0, 0, 0, 0.5);
  font-size: 20px;
  font-weight: bold;
  transition: all 0.3s ease;
}

.round-button:hover {
  background-color: rgba(0, 0, 0, 0.8);
  box-shadow: 0 0 10px rgba(255, 255, 100, 1);
  text-shadow: 0 0 10px rgba(255, 255, 100, 1);
}

.round-button {
  top: 50%;
  left: 50%;
  margin-top: -110px;
  margin-left: -100px;
  position: fixed;
}

@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  background-color: white;
  color: #333;
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
}

input[type="checkbox"] {
  display: none;
}
input[type="checkbox"]:checked + label {
  animation: loading-background 5s linear forwards;
}
input[type="checkbox"]:checked + label::after {
  animation: loading 5s linear forwards;
}

label {
  background-color: greenyellow;
  cursor: pointer;
  display: block;
  height: -50;
  left: 50%;
  margin-left: -5em;
  margin-top: -4em;
  position: absolute;
  text-align: center;
  width: 10em;
}
label::before {
  background-color: whitesmoke;
  content: "";
  display: block;
  height: 2.6em;
  margin: 0.2em;
  outline: 0.4em solid white;
  outline-offset: 0.2em;
  position: absolute;
  width: 9.6em;
}
label::after {
  content: "Sync Lyrics";
  line-height: 3em;
  position: relative;
}


