body {
  background-color: white;
  color: #333;
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
}

input[type="checkbox"] {
  display: none;
}
input[type="checkbox"]:checked + label {
  -webkit-animation: loading-background 5s linear forwards;
          animation: loading-background 5s linear forwards;
}
input[type="checkbox"]:checked + label::after {
  -webkit-animation: loading 5s linear forwards;
          animation: loading 5s linear forwards;
}

label {
  background-color: greenyellow;
  cursor: pointer;
  display: block;
  height: -50;
  left: 50%;
  margin-left: -5em;
  margin-top: -4em;
  position: absolute;
  text-align: center;
  width: 10em;
}
label::before {
  background-color: whitesmoke;
  content: "";
  display: block;
  height: 2.6em;
  margin: 0.2em;
  outline: 0.4em solid white;
  outline-offset: 0.2em;
  position: absolute;
  width: 9.6em;
}
label::after {
  content: "Sync Lyrics";
  line-height: 3em;
  position: relative;
}


